import React, {useEffect, useState} from 'react';
import AxiosURL from "../../Services/AxiosURL";
import {Link} from "react-router-dom";
import $ from "jquery";

const MainPage = () => {

    ////  ... 
    //paging  ...

    const [loading, setLoading] = useState(false);
    const [study, setStudy] = useState({});
    const [user, setUser] = useState('');

    $('.boxes').css('opacity','0');

    useEffect(() => {
        if (localStorage.getItem('user-info')) {

            setLoading(true);
            setUser(localStorage.getItem('user-info'))
            const token = JSON.parse(localStorage.getItem('user-info'))

                AxiosURL.studyList(token.authorization)
                    .then(response => {
                        setStudy(response.data.data.content)
                    }).catch(error => {
                    console.log(error)
                })
                setLoading(false);
        }
        setTimeout(() => {
            $('.boxes').css('opacity','1');
        }, 300);
    }, [])



    const boxes = [
        {
            topic:'',
            courseName:'  Python ',
            duration:'2022-04-13T17:24:10.81523',
            description:'/  '},
        {
            topic:'',
            courseName:'  C',
            duration:'2022-04-13T17:24:10.81523',
            description:'C , ,    !'},
        {
            topic:'',
            courseName:'Google   Kotlin',
            duration:'2022-04-13T17:24:10.81523',
            description:'Google   Kotlin   '},
        {
            topic:'',
            courseName:'All about  ',
            duration:'2022-04-13T17:24:10.81523',
            description:''},
        {
            topic:'',
            courseName:'React  ',
            duration:' 2022-04-13T17:24:10.81523',
            description:'JavaScript   !! React        '},
        {
            topic:'/',
            courseName:'  JavaScript',
            duration:' 2022-04-13T17:24:10.81523',
            description:'        ! ' +
                'JavaScript   ,  ,  JavaScript     !'},
        
        {
            topic:'/',
            courseName:'  HTML/CSS ',
            duration:'2022-04-13T17:24:10.81523',
            description:'    HTML/CSS    !'},
        {
            topic:'/',
            courseName:'  ',
            duration:'2022-04-13T17:24:10.81523',
            description:'   !'}
    ]
    

    return (
        <>
            {loading &&
                <></>
            }
            <br/>
            <br/>
                <div className="box_cont_main">
                    {study.length ?
                        study.map((study, key) => (
                            <div className="boxes" key={key}>
                                <div className="boxes_content">
                                    <span>{study.nickName}</span>
                                    <div className="boxes_courseName">{study.studyName}</div>
                                    <div className="classes_date">
                                        {study.regDate}
                                    </div>
                                    <p>{study.studyExplanation}
                                    </p>

                                    <hr/>
                                    <button type="button" className="add_classes">
                                        
                                    </button>
                                </div>
                            </div>
                        ))
                        :
                        <div className="box_cont_main" >
                            {boxes.map((box, key)=>(
                                <div className="boxes" key={key}>
                                    <div className="boxes_content">
                                        <span>{box.topic}</span>
                                        <div className="boxes_courseName">{box.courseName}</div>
                                        <div className="classes_date">
                                            {box.duration}
                                        </div>
                                        <p>{box.description}
                                        </p>

                                        <hr />
                                        <Link to = '/signup'>
                                        <button type="button" className="add_classes">
                                              
                                        </button>
                                        </Link>
                                    </div>
                                </div>
                            ))}

                        </div>
                    }
                </div>
        </>
    );
};

export default MainPage;